import React from "react";
import {
    CButton,
    CCol,
    CModal,
    CModalBody,
    CModalHeader,
    CModalTitle,
    CRow,
} from "@coreui/react";
import "Component/Modal/ModalConfirmation/index.scss";
const PopUpDiscard = ({ discard, moduleEntry, close, discardEntry, module }) => {
    return (
        <CRow onKeyDown={e => e.keyCode === 27 ? close() : false} className={discard ? null : 'd-none'}>
            <CCol onKeyDown={e => e.keyCode === 27 ? close() : false}>
                <CModal
                    onClose={() => close()}
                    show={discard}
                    size="lg"
                    closeOnBackdrop={false}
                >

                    <div className="d-flex flex-row-reverse pr-3 pt-3 mt-1">
                        <i onClick={() => close()} className='iconU-modalClose' ></i>
                    </div>

                    <CModalHeader className="px-5 pb-4 mb-2 pt-4 mt-2 border-none">
                        <CModalTitle className="red LatoBold">Discard? <span><i className="em em-astonished"></i></span></CModalTitle>
                        {/* <label onClick={() => close()} className='iconU-modalClose' /> */}
                    </CModalHeader>

                    <CModalBody className="px-5 pb-5 mb-4 py-0">
                        <p className="text mb-3 h5 respon-txt">
                            Are you sure you want to discard <span className='green'>{` ${moduleEntry?.jobPost.list.length} selected SKU ${moduleEntry?.jobPost.list.length > 1 ? '(s)' : ''}`}</span>
                            from this
                            <div className='green h5 pt-2 respon-txt'>{module}</div>
                        </p>
                        <br />
                        <CButton onClick={() => discardEntry(moduleEntry?.jobPost.id)} className="bg-background text no-shadow pl-4 pr-5 mr-3 LatoBlack">
                            Discard
                        </CButton>
                        <CButton onClick={() => close()} className="bg-green no-shadow pl-4 pr-4 LatoBlack">
                            Don't Discard
                        </CButton>{" "}
                    </CModalBody>
                </CModal>
            </CCol>
        </CRow>
    );
};

export default PopUpDiscard
