/* Stock Transfer have different flow, to check empty PO Code must input sku_uuid */

import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { withRouter } from "react-router";
import { CREATE_JOB_SHEET } from 'apiServices/action'
import Dropdown from "Component/Dropdown";
import Search from "Component/Search";
import { getStockTakeSummary, getUnfinishedEntries,removeEntryModule } from "apiServices";
import { getWarehouseArea, getRipenessLevel,getWarehouse } from "apiServices/dropdown";
import SKUItem from "Component/SKUItem";
import loading from "assets/icons/loading/LOADING-MLS-GRAY.gif";
import "./index.scss";
import {selectSku} from './services'
import { filterBySkuName, responseCheck, selectedSkuCheck } from "Page/StockEntry/GeneralServices/moduleServices";
import PopUp from "Component/Modal/PopUp";
import PopUpUnfinished from 'Component/Modal/PopUpUnfinished'
import Discard from 'Component/Modal/PopUpDiscard'

const StockTransfer = (props) => {
  const dispatch = useDispatch();
  const story = useHistory()
  const thereAreRequest = useSelector(state => state.thereAreRequest);
  const moduleFirstLoad = useSelector(state => state.stockTransferFirstLoad)
  const warehouseUuid = useSelector((state) => state.user.warehouseUuid);
  const status = useSelector((state) => state.status);
  const isLoading = useSelector((state) => state.isLoading);
  const moduleName = 'stockTransfer'
  const stockTransferEntryData = useSelector((state) => state.stockTransferEntryData);
  const [summary, setSummary] = useState(null)
  const [localEntryData, setLocalEntryData] = useState()
  const isCheckedTotal = selectedSkuCheck({ moduleEntry: localEntryData, moduleName });
  const [isCheckedBefore, setIsCheckedBefore] = useState(0)
  const added = isCheckedBefore !== isCheckedTotal && isCheckedTotal > 0 ? true : false
  const [search, setSearch] = useState()
  const [warehouse, setWarehouse] = useState()
  const [area,setArea] = useState()
  const [discard, setDiscard] = useState()
  console.log(stockTransferEntryData);

  const [warehouseId, setWarehouseId] = useState()
  const [warehouseIdDestination, setWarehouseIdDestination] = useState()
  const [areaId,setAreaId] = useState()
  const [areaIdDesposition, setAreaIdDesposition] = useState()
  const [areaMatched,setAreaMatched] = useState()

  useEffect(() => {
    async function getData() {
      const warehouseList = await getWarehouse({})
      const ripeness = await getRipenessLevel({ dispatch })
      const areaList = await getWarehouseArea({ dispatch, warehouseUuid })
      
      getUnfinishedEntries({ dispatch, moduleName, isExist: stockTransferEntryData ? true : false, ripeness })

      const warehouseSource = warehouseList.filter(wh => wh.value === warehouseUuid)
      setWarehouseId(warehouseSource[0]) 

      setArea(areaList)
      setWarehouse(warehouseList)
      setIsCheckedBefore(isCheckedTotal)
    }
    getData()
  }, []);

  useEffect( _ => {
    // if(areaId?.value && areaIdDesposition?.value && areaId?.value !== areaIdDesposition?.value && warehouseId && warehouseIdDestination && warehouseId !== warehouseIdDestination) setAreaMatched(true)
    if(areaId && warehouseId && warehouseIdDestination && warehouseId !== warehouseIdDestination) setAreaMatched(true)

    else setAreaMatched(false)
  },[areaId,areaIdDesposition,added, warehouseId,warehouseIdDestination])

  useEffect(()=> {
    setLocalEntryData()
    setSummary()
  },[areaId,areaIdDesposition,warehouseId,warehouseIdDestination])

  //search
  const [category, setCategory] = useState()
  const [sortBy, setSortBy] = useState()

  const dimention = document.documentElement.clientHeight
  const height = dimention - 400
  const emptyItem = dimention - 400
  return (
    <div className="w-100  stock-take-summary">
      <Discard
          discard={discard}
          module={'Inventory Entry - Stock Transfer'}
          moduleEntry={stockTransferEntryData}
          close={() => setDiscard(false)}
          discardEntry={(id) => removeEntryModule({ id, moduleName, dispatch,setDiscard })}
      />
      <PopUpUnfinished
        discard={discard}
        moduleFirstLoad={moduleFirstLoad}
        moduleEntry={stockTransferEntryData}
        redirect={() => props.history.push("/inventory-entry/stock-transfer/weight-entry")}
        previousPage={() => story.goBack()}
        setDiscard={() => setDiscard(true)}
      />
      <PopUp status={status} />
      <div className="px-3 pt-3 bg-white border-all radius-all d-md-flex dd-group">
      <div className="col-lg-3 px-0 stock-take-dropdown">
          <Dropdown
            show={true}
            isDisabled={true}
            title="Warehouse Source"
            placeholder='Select Warehouse Source'
            options={warehouse}
            onChangeDropdown={(selected) => setWarehouseId(selected)}
            selectedValue={warehouseId}
            showTitle={true}
          />
        </div>
        <div className="col-lg-3 px-0 stock-take-dropdown">
          <Dropdown
            show={true}
            title="Area Source"
            placeholder='Select Area Source'
            options={area?.filter(data => data.label !== 'Waste')}
            onChangeDropdown={(selected) => setAreaId(selected) }
            selectedValue={areaId}
            showTitle={true}
          />
        </div>
        <div className="col-lg-3 px-0 stock-take-dropdown">
          <Dropdown
            show={true}
            title="Warehouse Destination"
            placeholder='Select Warehouse Destination'
            options={warehouse?.filter( data => data?.label !== warehouseId?.label)}
            onChangeDropdown={(selected) => setWarehouseIdDestination(selected)}
            selectedValue={warehouseIdDestination}
            showTitle={true}
          />
        </div>
        {/* <div className="col-lg-3 px-0 stock-take-dropdown">
          <Dropdown
            show={true}
            title="Area Destination"
            placeholder='Select Area Destination'
            options={area}
            onChangeDropdown={(selected) => setAreaIdDesposition(selected)}
            selectedValue={areaIdDesposition}
            showTitle={true}
          />
        </div> */}
      </div>

      <div className="pt-3 pb-0 pl-3 pr-3 bg-white mt-3 border-all radius-all mb-3">
        <div className="d-md-flex d-lg-flex mb-3 justify-content-between">
          <div className="text LatoBold d-flex justify-content-between align-items-center sku-des">
            <div>
              Enter SKU or Description{" "}
              <span className="green">{`(${isCheckedTotal} Selected SKU${isCheckedTotal > 1 ? "s" : ""
                })`}</span>
            </div>
            {/* <div className="icon-unfinishEn">
              <i className="iconU-sideBarInventoryEntry emptyTextField"></i>
            </div> */}
          </div>
        </div>
        <Search
          setSortBy={setSortBy}
          category={category}
          setCategory={setCategory}
          placeholder={"Enter SKU"}
          filter={false}
          searchHandler={(desc) =>
            areaId?.value && warehouseId?.value && warehouseIdDestination?.value && warehouseId?.value !== warehouseIdDestination?.value ?
              getStockTakeSummary({category, dispatch, desc, warehouseUuid, areaId, areaIdDesposition ,setModule: setSummary, module:localEntryData },setSearch(desc)) : (!areaId?.value ? setAreaId('empty' ) : (areaId?.value === areaIdDesposition?.value ? setAreaIdDesposition('empty') : (!warehouseId?.value ? setWarehouseId('empty') : (!warehouseIdDestination?.value ? setWarehouseIdDestination('empty') : (warehouseIdDestination?.value === warehouseId?.value ? setWarehouseIdDestination('empty') : null)))))
          }
        />
        <div
          className={`mb-3 mt-3 d-flex align-items-center scroll-sku ${summary?.length ? "flex-column" : "flex-row"
            } w-100 text-center`}
            style={{ minHeight: height , maxHeight:height}}
        >
          {summary?.length ? (
            summary?.sort( function(a,b){ return filterBySkuName(a,b,sortBy?.value)})?.map((data, idx) => {
              return (
                <SKUItem
                  key={idx}
                  img={data?.photo}
                  skuDescription={data?.skuName}
                  skuNumber={data?.skuNumber}
                  uomLabel="weight"
                  uomValue={data?.totalQty}
                  uom={data?.unitOfMeassures}
                  isCheckedHandler={(checked) =>
                    selectSku({
                      idx,
                      checked,
                      dispatch,
                      setLocalEntryData,
                      moduleSummary: summary,
                      moduleEntry: localEntryData,
                      moduleName,
                      warehouseUuid,
                      warehouseIdDestination,
                      areaId,
                      areaIdDesposition,
                    })
                  }
                  check={data?.checked}
                />
              );
            })
          ) : (
              <div className="w-100 text-center emptyTextField d-flex flex-column align-items-center justify-content-center" style={{ minHeight: emptyItem,maxHeight: emptyItem}}>
                {!responseCheck({ isLoading, summary }) ? (
                  <img className="loading" src={loading} alt="loading" />
                ) : areaId && areaIdDesposition && summary?.length === 0 ? (
                  <div>
                    Sorry, we couldn't find any matches
                    for '<span className='LatoBold'>{search}</span>' in {areaId && areaIdDesposition?.label} area.
                    <br/>
                    Please Try search with another term.
                  </div>
                ) : 'Enter SKU'}
              </div>
            )}
        </div>
      </div>
      <div className="h-screen-summary-footer d-flex flex-column justify-content-start">
        <div className="w-100 d-flex align-items-center justify-content-between mb-1">
         <div>
            <button
            onClick={() => props.history.push("/inventory-entry/stock-transfer/CSV-upload")}
            type="button"
            className="btn px-4 no-shadow border-all bg-white fontSizeButton"
          >
            Upload File
          </button>
         </div>
          <button
            onClick={() => {
              if (thereAreRequest === 0 && (localEntryData && localEntryData?.jobPost?.list?.length > 0) && areaMatched === true) {
                dispatch({ type: CREATE_JOB_SHEET, stockTransferEntryData: localEntryData })
                props.history.push("/inventory-entry/stock-transfer/weight-entry")
              }
            }
            }
            type="button"
            className={`btn px-5 fontSizeButton no-shadow text-white ${thereAreRequest === 0 && (localEntryData && localEntryData?.jobPost?.list?.length > 0) && areaMatched === true ? "bg-green" : "bg-lineGrey"
              }`}
          >
            {(!localEntryData || localEntryData?.jobPost?.list?.length === 0) && thereAreRequest === 0 ? 'Next' :thereAreRequest === 0 && (localEntryData && localEntryData?.jobPost?.list?.length > 0) ? 'Next' : 'Processing...'}
          </button>
        </div>
      </div>
    </div>
  );
};

export default withRouter(StockTransfer);
